      <h1>头条</h1>
      <h2 style="display: flex">劳动节</h2>
      <p>五一国际劳动节（International Workers' Day；International Labour Day；May
        Day），是世界上80多个国家的全国性节日、全世界劳动人民团结战斗的节日，时间为5月1日（某些国家为其他时间，如美国、 [24]加拿大 [25]）。 [14]</p>
      <p>五一国际劳动节是工人阶级奋斗争取来的。随着资本主义进入垄断阶段，工人每天被迫进行大量的劳动，为了维护自己的权利，工人们决定斗争。
        [6-7]1886年，美国芝加哥等城市的35万工人举行大罢工和游行示威，要求实行8小时工作制、改善劳动条件，美国当局迫于国际舆论和社会压力，宣布实施8小时工作制。为了纪念这次斗争，1889年7月，第二国际在巴黎举行的代表大会将5月1日确立为五一国际劳动节。
      </p>
      <p>五一国际劳动节具有重要意义，劳动者通过斗争，用顽强、英勇不屈的奋斗精神，争取到合法权益，是人类文明民主的历史性进步。 [12] [43]为庆祝五一国际劳动节，中国共产党和政府对有突出贡献的劳动者进行表彰， [21]
        [28]举行集会或文体活动。 [18] [24]在国际上，有的国家会在五一当天举行游行示威 [26] [27]，有的国家则有独特的民俗活动。</p>
      <p>1949年12月23日，中央人民政府政务院将五一国际劳动节定为值得纪念和庆祝的节日，放假1天。 [10] [18]而后放假时间不断调整，1999年调整为7天，形成黄金周。
        [56]2008年后，五一假期开始缩减，曾出现放3天、4天、5天的情况。</p>
